html,body{
  margin:0;
  padding:0;
  width:100%;
  height:100%;
  scroll-behavior:smooth !important;
  line-height:1.5;
  background-color:#fff;
  -webkit-text-size-adjust:100%;
  -webkit-tap-highlight-color:rgba(0,0,0,0);
  overflow-x:hidden;
  font-family:'Poppins', BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif !important
}

.customShadow{
  box-shadow:rgba(0,0,0,0.15) 0 4px 12px
}

.normal-hover:hover{
  --tw-gradient-from: #ec4899;
  --tw-gradient-stops: var(--tw-gradient-from),
    var(--tw-gradient-to, rgba(236, 72, 153, 0));
  --tw-gradient-to: #f59e0b;
  background-image:linear-gradient(to right, var(--tw-gradient-stops));
  -webkit-text-fill-color:transparent;
  background-clip:text;
  -webkit-background-clip:text
}

.gradient-border{
  --tw-gradient-from: #ec4899;
  --tw-gradient-stops: var(--tw-gradient-from),
    var(--tw-gradient-to, rgba(236, 72, 153, 0));
  --tw-gradient-to: #f59e0b;
  border-style:solid;
  border-image-slice:1;
  border-radius:999000;
  border-image-source:linear-gradient(to right, var(--tw-gradient-stops))
}

.gradient-border.bottom{
  border-bottom:1px
}

.gradient-border.full{
  border:1px
}

.hover\:gradient-border:hover{
  --tw-gradient-from: #ec4899;
  --tw-gradient-stops: var(--tw-gradient-from),
    var(--tw-gradient-to, rgba(236, 72, 153, 0));
  --tw-gradient-to: #f59e0b;
  border-style:solid;
  border-image-slice:1;
  border-radius:999000;
  border-image-source:linear-gradient(to right, var(--tw-gradient-stops))
}

.hover\:gradient-border:hover.bottom{
  border-bottom:1px
}

.hover\:gradient-border:hover.full{
  border:1px
}

.gradient-text{
