.feature .feature-icon{
  transition:all 0.2s ease-in-out
}

.feature:hover .feature-icon{
  -webkit-transform:translateY(-10px);
  transform:translateY(-10px)
}

.tooltip-ref:hover .tooltip-container{
  display:block
}

.tooltip-ref .tooltip-container{
  display:hidden
}

.custom-scroll-mini::-webkit-scrollbar{
  width:0.5rem
}

.custom-scroll-mini::-webkit-scrollbar-track{
  box-shadow:inset 0 0 6px rgba(0,0,0,0.3)
}

.custom-scroll-mini::-webkit-scrollbar-thumb{
  background-color:#1f2937;
  border-radius:50px
}

.custom-scroll-mini.darker::-webkit-scrollbar-thumb{
  background-color:#374151
}

.dark ::-webkit-scrollbar{
  width:1rem
}

.dark ::-webkit-scrollbar-track{
  box-shadow:inset 0 0 6px rgba(0,0,0,0.3)
}

.dark ::-webkit-scrollbar-thumb{
  background-color:#374151;
  border-radius:10px
}

.dark ::-webkit-scrollbar-track-piece{
  background-color:#1f2937
}

.dark .scroll-dark-md::-webkit-scrollbar{
  width:1rem
}

.dark .scroll-dark-md::-webkit-scrollbar-track{
  box-shadow:inset 0 0 6px rgba(0,0,0,0.3)
}

.dark .scroll-dark-md::-webkit-scrollbar-thumb{
  background-color:#4b5563;
  border-radius:0
}

.dark .scroll-dark-md::-webkit-scrollbar-track-piece{
  background-color:#374151
}

.divider{
  border-bottom-width:1px;
  --tw-border-opacity: 1;
  margin-bottom:1rem;
  padding-bottom:1rem;
  border-color:rgba(229, 231, 235, var(--tw-border-opacity))
}

.dark .dark\:divider{
  --tw-border-opacity: 1;
  border-bottom-width:1px;
  --tw-border-opacity: 1;
  margin-bottom:1rem;
  padding-bottom:1rem;
  border-color:rgba(55, 65, 81, var(--tw-border-opacity))
}

.on-hover-container .on-hover-item{
  display:none
}

.on-hover-container:hover .on-hover-item{
  display:block
}

#home_search_panel{
  box-shadow:none;
  border-color:transparent
}

button{
  outline:none !important
}

button:focus{
  outline:none !important
}

.border-gradient{
  background-image:linear-gradient(#1f2937, #1f2937),radial-gradient(circle at top left, #ec4899, #f59e0b);
  background-origin:border-box;
  background-clip:content-box, border-box
}

.customDropDown{
  position:relative;
  display:inline-block
}

.dropdown-content{
  display:none;
  position:absolute;
  box-shadow:0 10px 20px 0 rgba(0,0,0,0.05);
  padding:12px 16px;
  z-index:10;
  border-radius:8px
}

.customDropdown:hover .dropdown-content{
  display:block
}

